
const contactInit = () => {
    const senderName = document.getElementById('name');
    const senderEmail = document.getElementById('email');
    const senderMesssage = document.getElementById('message');
    const sendMessageBtn = document.getElementById('send-message');

    const errorMessage = document.getElementById('error-message');

    sendMessageBtn.addEventListener('click', e => {
        e.preventDefault();

        if(!senderName.value || !senderEmail.value || !senderMesssage.value){
            errorMessage.textContent = 'Please fill all the fields!';
            return 0;
        }

        const templateParams = {
            name: senderName.value,
            email: senderEmail.value,
            message: senderMesssage.value
        }

        emailjs.send('service_mbegipk', 'template_iq2z9ir', templateParams, 'user_GhQKAxrMddSuilr5XuXZ6').then(response => {
            console.log(response.status, response.text);
            errorMessage.textContent = 'Message sent, Thank you!';
            senderName.value = '';
            senderEmail.value = '';
            senderMesssage.value = '';
        }, err => {
            errorMessage.textContent = 'Something went wrong! Please try again';
        })
    })
}


export default contactInit;